import React, { Component } from 'react';
import Slider from "react-slick";
import PaintsMap from '../data/paintsMap'


import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const styles = theme => ({
    root: {
        ...theme.mixins.gutters(),
        padding: theme.spacing.unit * 2,
    },
});


class Paint extends React.Component {
    constructor(props) {
        super(props);
    
        console.log(this.props)
    }
    state = {
        paint: '',
        activeSlide: 0,
        activeSlide2: 0,
        headerImage: {}
    }
    render() {
        
        const { classes } = this.props;  
        if (PaintsMap[sessionStorage.getItem("make")] &&
            PaintsMap[sessionStorage.getItem("make")][sessionStorage.getItem("year")] &&
            PaintsMap[sessionStorage.getItem("make")][sessionStorage.getItem("year")][sessionStorage.getItem("model")]) {    
            const paintsMap = PaintsMap[sessionStorage.getItem("make")][sessionStorage.getItem("year")][sessionStorage.getItem("model")]
            var settings = {
                dots: true,
                beforeChange: (current, next) => {    
                    const paint = paintsMap[next]
                    sessionStorage.setItem('paint', Object.keys(paint))
                    this.setState({headerImage: paint[Object.keys(paint)[0]]}, () => {
                        this.props.handlerToUpdate(this.state.headerImage)
                    })                
                    this.setState({ activeSlide: next })
                },
                afterChange: current => this.setState({ activeSlide2: current })
            };
                  
            const paints = paintsMap.map((paint, idx) => {                
                return (
                    <div key={idx} >                                               
                        <img src={paint[Object.keys(paint)]} />
                        {Object.keys(paint)} 
                    </div>

                )
            })
            
            return (
            <div className="container">            
                <Slider {...settings}>   
                    {paints}                
                </Slider>
            </div>
            );
        }
        else {
            return (<div/>)
        }
        
    }
}
  
export default Paint;

