import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { withRouter } from "react-router";
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormLabel from '@material-ui/core/FormLabel';
import Grid from '@material-ui/core/Grid';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Paper from '@material-ui/core/Paper';
import PropTypes from 'prop-types';
import Select from '@material-ui/core/Select';
import Switch from '@material-ui/core/Switch';
import Typography from '@material-ui/core/Typography';
import blue from '@material-ui/core/colors/blue';
import pink from '@material-ui/core/colors/pink';
import purple from '@material-ui/core/colors/purple';
import Financials from './financials';
import Button from '@material-ui/core/Button';
import Paint from './paint';

const styles = theme => ({
    root: {
      ...theme.mixins.gutters(),
      padding: theme.spacing.unit * 2,
    },
    formControl: {
      margin: theme.spacing.unit,
      minWidth: 120,
    },
    disclaimer: {
      fontSize:13,
      marginBottom: theme.spacing.unit * 2
    },
    selectEmpty: {
      marginTop: theme.spacing.unit * 2,
    },
    colorSwitchBase: {
      color: purple[300],
      '&$colorChecked': {
        color: purple[500],
        '& + $colorBar': {
          backgroundColor: purple[500],
        },
      },
    },
    colorBar: {},
    colorChecked: {},
    iOSSwitchBase: {
      '&$iOSChecked': {
        color: theme.palette.common.white,
        '& + $iOSBar': {
          backgroundColor: blue[500],
        },
      },
      transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
        easing: theme.transitions.easing.sharp,
      }),
    },
    iOSChecked: {
      transform: 'translateX(15px)',
      '& + $iOSBar': {
        opacity: 1,
        border: 'none',
      },
    },
    iOSBar: {
      borderRadius: 13,
      width: 42,
      height: 26,
      marginTop: -13,
      marginLeft: -21,
      border: 'solid 1px',
      borderColor: blue[400],
      backgroundColor: pink[500],
      opacity: 1,
      transition: theme.transitions.create(['background-color', 'border']),
    },
    iOSIcon: {
      width: 24,
      height: 24,
    },
    iOSIconChecked: {
      boxShadow: theme.shadows[1],
    },
  });

  class Options extends Component {
    state = {
      exteriorColor: '',
      interiorColor: '',
      checkedBed: true,
      checkedHeater: true,
      checkedFridge: true,
      checkedSatelite: true,
      checkedCollision: true      

    };  
  
    handleChange = event => {
      this.setState({ [event.target.name]: event.target.value });
    };
  
    handleChangeSwitch = name => event => {
      this.setState({ [name]: event.target.checked });
      
    };
  
    handleSubmit = event => {
      // https://us-central1-winter-agency-229213.cloudfunctions.net/function-2
      
      
          
    }
    handleBack = event => {
      this.props.history.push('/start/');
    }

    handlePaintsHeader = (paint) => {  
      console.log(paint)
      this.props.handlerToUpdate( paint );       
    } 

    render() {
      const { classes } = this.props;       
      return (
          <Paper className={classes.root} elevation={1}>
            <FormLabel className={classes.disclaimer} component="legend">
              HELP US BEST SERVE YOU IN GETTING YOU AS CLOSE TO WHOLESALE PRICING AS POSSIBLE. YOUR ANSWERS TO THE QUESTIONS BELOW GIVE US A BASELINE IDEA OF WHERE YOU WANT TO BE AND HOW WE CAN BEST SERVE YOU
            </FormLabel>
            <Typography component={'span'} variant="h5" >
              {sessionStorage.getItem("make") + ' ' + sessionStorage.getItem("year") + ' ' + sessionStorage.getItem("model")}
            </Typography>    
            <form className={classes.root} autoComplete="off">
            <Grid container spacing={24}>
              <Grid item xs={6}>
                <Paper className={classes.paper}>
                  <FormControl className={classes.formControl}>
                    <InputLabel htmlFor="interiorColor">interior color</InputLabel>
                    <Select
                      value={this.state.interiorColor}
                      onChange={this.handleChange}
                      inputProps={{
                        name: 'interiorColor',
                        id: 'interiorColor',
                      }}
                    >
                      <MenuItem value="">
                        <em>None</em>
                      </MenuItem>
                      <MenuItem value="blue">Blue</MenuItem>
                      <MenuItem value="green">Green</MenuItem>
                      <MenuItem value="red">Red</MenuItem>
                    </Select>
                  </FormControl>
                </Paper>
              </Grid>
              
              <Grid item xs={6}>
                <Paper className={classes.paper}>
                  <FormLabel component="legend">Paint Packages</FormLabel>
                      <Paint handlerToUpdate = {this.handlePaintsHeader} />
                  
                </Paper>
              </Grid>
              <Grid item xs={12}>  
              <Button                
                  onClick={this.handleBack}
                  className={classes.button}
                >
                  Back
                </Button>            
                <Financials />
              </Grid>
            </Grid>
            </form>          
          </Paper> 
      );
    }
  }
    
  Options.propTypes = {
    classes: PropTypes.object.isRequired,
  };
  export default withRouter(withStyles(styles)(Options))